import React from "react";
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Button } from "@/components/ui/button";
import Link from "next/link";

type ModuleCardProps = {
  id: string;
  title: string;
  description: string;
  progress: number; // percentage value between 0-100
};

const ModuleCard = ({ id, title, description, progress }: ModuleCardProps) => {
  // Validate props and provide fallbacks
  const safeTitle = title || "Untitled Module";
  const safeDescription = description || "No description available.";
  const safeProgress = Math.max(0, Math.min(100, progress || 0));

  return (
    <Card className="h-full flex flex-col hover:shadow-lg transition-shadow duration-200">
      <CardHeader className="pb-4">
        <CardTitle className="text-xl font-semibold text-gray-900 leading-tight">
          {safeTitle}
        </CardTitle>
      </CardHeader>
      
      <CardContent className="flex-1 pb-4">
        <p className="text-gray-600 text-sm leading-relaxed mb-6">
          {safeDescription}
        </p>
        
        <div className="space-y-2">
          <div className="flex items-center justify-between text-xs text-gray-500">
            <span className="font-medium">Progress</span>
            <span className="font-medium">{safeProgress}%</span>
          </div>
          <Progress value={safeProgress} className="h-2" />
          {safeProgress === 0 && (
            <p className="text-xs text-gray-400 mt-1">Not started</p>
          )}
          {safeProgress > 0 && safeProgress < 100 && (
            <p className="text-xs text-blue-600 mt-1">In progress</p>
          )}
          {safeProgress === 100 && (
            <p className="text-xs text-green-600 mt-1">Completed</p>
          )}
        </div>
      </CardContent>
      
      <CardFooter className="pt-4 border-t border-gray-100">
        <div className="flex gap-2 w-full">
          <Link href={`/modules/${id}`} className="flex-1">
            <Button 
              variant="default" 
              size="sm" 
              className="w-full"
            >
              {safeProgress === 0 ? "Start Module" : "Continue"}
            </Button>
          </Link>
          <Link href={`/modules/${id}/overview`}>
            <Button 
              variant="outline" 
              size="sm"
            >
              Preview
            </Button>
          </Link>
        </div>
      </CardFooter>
    </Card>
  );
};

export default ModuleCard;
